.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 16px;

  @include onTable {
    grid-template-columns: repeat(8, 1fr);
    column-gap: 24px;

  }

  &__item {
    @for $start from 1 through 8 {
      @for $end from $start  through 8 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1};
        }

        &--table--#{$start}-#{$end} {
          @include onTable {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }

      & {
        @for $start from 1 through 8 {
          @for $end from $start  through 8 {

            &--desktop--#{$start}-#{$end} {
              @include onDesktop {
                grid-column: #{$start} / #{$end + 1};
              }
            }
          }
        }
      }
    }
  }
}
